  max-height: 425px;
  min-height: 120px;
  overflow-x: hidden;
  padding-right: 10px;
  font-family: "Quicksand", sans-serif;
}

/* width */
._RZMQZ::-webkit-scrollbar {
  width: 5px;
}

/* Track */
._RZMQZ::-webkit-scrollbar-track {
  border-radius: 10px;
}

/* Handle */
